import React, { Component } from "react";
import FormPersonalDetails from "./FormPersonalDetails";
import Confirm from "./Confirm";

export class Userform extends Component {
  state = {
    step: 1,
    name: "",
    phone: "",
    pickuplocation: "",
    droplocation: "",
    vehicle: "",
    vehiclevalue: "",
  };

  // proceed to next steps
  nextStep = () => {
    const { step } = this.state;
    this.setState({
      step: step + 1,
    });
  };

  //go back to prev state
  prevStep = () => {
    const { step } = this.state;
    this.setState({
      step: step - 1,
    });
  };

  // Handle Fields CHange

  handleChange = (input) => (e) => {
    this.setState({ [input]: e.target.value });
  };

  render() {
    const { step } = this.state;
    const { name, phone, pickuplocation, droplocation, vehicle,  } = this.state;
    const values = {
      name,
      phone,
      pickuplocation,
      droplocation,
      vehicle,
      
    };
    const {
      vehiclevalue,
    } =this.state;
    switch (step) {
      case 1:
        return (
          <FormPersonalDetails
            nextStep={this.nextStep}
            handleChange={this.handleChange}
            values={values}
            vehiclevalue={vehiclevalue}
          />
        );
      case 2:
        return (
          <Confirm
            prevStep={this.prevStep}
            nextStep={this.nextStep}
            values={values}
            vehiclevalue={vehiclevalue}
          />
        );

      case 3:
        return <h1>Your Form has been submitted</h1>;
    }
  }
}
export default Userform;
