import React, { Component } from "react";
import MuiThemeProvider from "material-ui/styles/MuiThemeProvider";
import { List, ListItem } from "material-ui/List";
import RaisedButton from "material-ui/RaisedButton";
import { db } from "../firebase";

export class Confirm extends Component {
  continue = (e) => {
    e.preventDefault();
    //adding databse
    const {
      name,
      phone,
      pickuplocation,
      droplocation,
      vehicle,
      
    } = this.props.values;
db.collection('forms').add({
  name: name,
  phone: phone,
  pickuplocation: pickuplocation,
  droplocation: droplocation,
  vehicle: vehicle,
}).then(() => {
  alert("Form Submitted");
})
    this.props.nextStep();
  };

  back = (e) => {
    e.preventDefault();
    this.props.prevStep();
  };

  render() {
    const {
      values: { name, phone, pickuplocation, droplocation, vehicle },
    } = this.props;
    

    return (
      <MuiThemeProvider>
        <React.Fragment>
          <List>
            <ListItem primaryText="Name" secondaryText={name} />
            <ListItem primaryText="Phone" secondaryText={phone} />
            <ListItem
              primaryText="Pickup Location"
              secondaryText={pickuplocation}
            />
            <ListItem
              primaryText="Drop Location"
              secondaryText={droplocation}
            />
            <ListItem primaryText="Vehicle" secondaryText={vehicle} />
          </List>
          <RaisedButton
            label="Confirm & Continue"
            primary={true}
            style={styles.button}
            onClick={this.continue}
          />
          <br />
          <RaisedButton
            label="Back"
            primary={false}
            style={styles.button}
            onClick={this.back}
          />
        </React.Fragment>
      </MuiThemeProvider>
    );
  }
}

const styles = {
  button: {
    margin: 15,
  },
};

export default Confirm;
