import React, { Component, useState } from "react";
import MuiThemeProvider from "material-ui/styles/MuiThemeProvider";
import TextField from "material-ui/TextField";
import RaisedButton from "material-ui/RaisedButton";
import { Form, FormContent, FormWrapper } from "../UserInput/UserinputElements";
import Dropdown from "react-dropdown";
import "react-dropdown/style.css";
import { vehicleList } from "../vehiclelist";

class NewForm extends Component {
  state = {
    name: "",
    phone: "",
    pickuplocation: "",
    droplocation: "",
    vehicle: "",
    vehiclevalue:  vehicleList ,
  };
  handleChange = (input) => (e) => {
    this.setState({ [input]: e.target.value });
  };
  handleDropdownChange = e => {
    this.setState({value: e.target.value})
  }

  render(name, phone, pickuplocation, droplocation, vehicle, vehiclevalue) {
    
  
    return (
      <form>
        <FormWrapper>
          <FormContent>
            <Form>
              <MuiThemeProvider>
                <React.Fragment>
                  <h1>Enter Your Detail</h1>
                  <br />
                  <TextField
                    class="text-field"
                    hintText="Enter Your Name"
                    onChange={this.handleChange}
                    
                  />

                  <br />
                  <TextField
                    hintText="Enter Your Phone Number"
                    onChange={this.handleChange}
                    
                  />

                  <br />
                  <TextField
                    hintText="Pickup Location"
                    floatingLabelFixed="Pickup Location"
                    onChange={this.handleChange}
                    
                  />
                  <br />
                  <TextField
                    hintText="Drop Location"
                    floatingLabelFixed="Drop Location"
                    onChange={this.handleChange}
                    
                  />
                  <br />
                  <TextField
                    hintText="Selected Vehicle"
                    floatingLabelFixed="Drop Location"
                    
                  />
                  <br />

                  <div>
                    <div>
                      <div>
                        <select
                          id="dropdown"
                          onChange={this.handleDropdownChange}
                        >
                          {vehicleList.map((value, label) => {
                            <option key={label} value={value}>
                              {value}
                            </option>;
                          })}
                        </select>
                      </div>

                      <div>Selected value is : {this.state.selectValue}</div>
                    </div>
                  </div>
                  <RaisedButton
                    label="Continue"
                    primary={true}
                    style={styles.button}
                    onClick={this.continue}
                  />
                  <RaisedButton
                    label="Back To Home"
                    primary={false}
                    style={styles.button}
                    onClick={this.home}
                  />
                </React.Fragment>
              </MuiThemeProvider>
            </Form>
          </FormContent>
        </FormWrapper>
      </form>
    );
  }
}
const styles = {
  button: {
    margin: 25,
  },
};
export default NewForm;
