import React, { Component, useState } from "react";
import MuiThemeProvider from "material-ui/styles/MuiThemeProvider";
import TextField from "material-ui/TextField";
import RaisedButton from "material-ui/RaisedButton";
import { Form, FormContent, FormWrapper } from "../UserInput/UserinputElements";
import Dropdown from "react-dropdown";
import "react-dropdown/style.css";
import { vehicleList } from "../vehiclelist";
import { db } from "../firebase";

export class FormPersonalDetails extends Component {
  constructor(props) {
    super(props);
    this.state = {
      selectValue: "",
    };
    this.handleDropdownChange = this.handleDropdownChange.bind(this);
  }
  continue = (e) => {
    e.preventDefault();
    //adding databse
    const {
      name,
      phone,
      pickuplocation,
      droplocation,
      vehicle,
    } = this.props.values;
    db.collection("forms")
      .add({
        name: name,
        phone: phone,
        pickuplocation: pickuplocation,
        droplocation: droplocation,
        vehicle: vehicle,
      })
      .then(() => {
        alert("Form Submitted");
      });
  };
  handleDropdownChange(e) {
    this.setState({ selectValue: e.target.value });
    console.log(e);
    e.preventDefault();
  }

  home = (e) => {
    e.preventDefault();
  };

  render() {
    const { values, handleChange } = this.props;

    return (
      <FormWrapper>
        <FormContent>
          <Form>
            <MuiThemeProvider>
              <React.Fragment>
                <h1>Enter Your Details</h1>
                <br />
                <TextField
                  class="text-field"
                  hintText="Enter Your Name"
                  onChange={handleChange("name")}
                  defaultValue={values.name}
                />

                <br />
                <TextField
                  hintText="Enter Your Phone Number"
                  onChange={handleChange("phone")}
                  defaultValue={values.phone}
                />

                <br />
                <TextField
                  hintText="Pickup Location"
                  floatingLabelFixed="Pickup Location"
                  onChange={handleChange("pickuplocation")}
                  defaultValue={values.pickuplocation}
                />
                <br />
                <TextField
                  hintText="Drop Location"
                  floatingLabelFixed="Drop Location"
                  onChange={handleChange("droplocation")}
                  defaultValue={values.droplocation}
                />
                <br />
                <TextField
                  hintText="Selected Vehicle"
                  floatingLabelFixed="Drop Location"
                  onFocus={handleChange("vehicle")}
                  value={this.state.selectValue}
                />
                <br />

                <div>
                  <div>
                    <div>
                      <select
                        id="dropdown"
                        onChange={this.handleDropdownChange}
                      >
                        <option value="Select Your Vehicle">
                          Select Vehicle
                        </option>
                        <option value="TATA ACE">TATA ACE</option>
                        <option value="ASHOK LEYLAND DOST">1</option>
                        <option value="3">2</option>
                        <option value="33">3</option>
                        <option value="4">4</option>
                      </select>
                    </div>

                    <div>Selected value is : {this.state.selectValue}</div>
                  </div>
                </div>
                <RaisedButton
                  label="Continue"
                  primary={true}
                  style={styles.button}
                  onClick={this.continue}
                />
                <RaisedButton
                  label="Back To Home"
                  primary={false}
                  style={styles.button}
                  onClick={this.home}
                />
              </React.Fragment>
            </MuiThemeProvider>
          </Form>
        </FormContent>
      </FormWrapper>
    );
  }
}

const styles = {
  button: {
    margin: 25,
  },
};

export default FormPersonalDetails;
