import styled from 'styled-components'

export const Nav = styled.nav`
display: flex;
flex-direction: row;
padding: 10px;
color: #000;
background-color: #ebebef;
height: 80px;
justify-content: center;
text-align: center;
align-items: center;

@media screen and(max-width: 720px){
    display: flex;
    flex-direction: column;
}
`