import "./App.css";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import Home from "./pages/index";
import SigninPage from "./pages/signin";
import UserInput from "./pages/userinput";
import Vendor from "./pages/vendor";
import Dashboard from "./components/Vendor/Dashboard";
import MyProfile from "./components/Vendor/MyProfile";
import MyOrders from "./components/Vendor/MyOrders";
import Billing from "./components/Vendor/Billing";
import Support from "./components/Vendor/Support";
import Complaint from "./components/Vendor/Complaint";

function App() {
  return (
    <Router>
      <Switch>
        <Route path="" component={Home} exact />
        <Route path="/signin" component={SigninPage} exact />
        <Route path="/userinput" component={UserInput} exact />
        <Route path="/vendor" component={Vendor} exact />
        <Route path="/dashboard" component={Dashboard} exact />
        <Route path="/myprofile" component={MyProfile} exact />
        <Route path="/myorders" component={MyOrders} exact />
        <Route path="/billing" component={Billing} exact />
        <Route path="/support" component={Support} exact />
        <Route path="/complaint" component={Complaint} exact />
      </Switch>
    </Router>
  );
}

export default App;
